@font-face {
  font-family: 'Roboto Condensed';
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'PT Mono';
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto Condensed';
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

* {
  box-sizing: border-box; }

body {
  font: 16px/1.375 "PT Mono", sans-serif;
  color: #666666;
  overflow-x: hidden; }

.wrapper {
  max-width: 1412px;
  box-sizing: content-box;
  padding: 0 30px;
  margin: 0 auto;
  position: relative; }

.wrapper-min {
  max-width: 1170px; }

li {
  list-style: none; }

a {
  color: inherit;
  text-decoration: none; }

p {
  margin-top: 0; }

.button {
  cursor: pointer;
  background: none;
  font: bold 14px/1 "Roboto Condensed", sans-serif;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 5px;
  padding: 14px 33px;
  color: inherit;
  transition: all 0.3s; }

.hidden {
  visibility: hidden; }

.main-header {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  color: #fff;
  padding: 20px 0; }
  .main-header__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  .main-header__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .main-header__menu-item {
    margin-left: 88px; }
  .main-header__menu-item:last-of-type {
    margin-left: 38px; }
  .main-header__menu-link {
    display: flex;
    align-items: center; }
  .main-header__menu-icon {
    margin-right: 15px; }
  .main-header__button {
    border: 1px solid #fff;
    color: #fff; }

.top-block {
  height: 100vh;
  background: #1B2D37 url("../img/top-block-background.png") no-repeat 100% 100%;
  color: #fff;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-x: hidden; }
  .top-block__wrapper {
    display: flex;
    align-items: center;
    position: relative;
    height: 100%; }
  .top-block__title {
    font: bold 80px/1 "Roboto Condensed", sans-serif;
    text-transform: uppercase;
    margin: 0 0 20px 0; }
  .top-block__subtitle {
    font-size: 22px;
    line-height: 1.5;
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start; }
  .top-block__subtitle-icon {
    width: 95px;
    padding-right: 28px;
    margin-top: 15px; }
  .top-block__subtitle-text {
    width: calc(100% - 95px); }
  .top-block__button {
    font-size: 16px;
    background: #00B2FF;
    border: none;
    margin-left: 95px;
    padding: 20px 45px; }
  .top-block__button:hover {
    background: #008bc7; }
  .top-block__img {
    position: relative;
    right: -80px; }
  .top-block__scroll-down {
    display: inline-block;
    color: #37434a;
    font-size: 14px;
    transform: rotate(-90deg);
    position: absolute;
    left: 0;
    bottom: 14%;
    cursor: pointer; }

.main-footer__block--top {
  padding: 65px 0 60px 0;
  border-bottom: 1px solid #E5E5E5;
  font-size: 15px;
  line-height: 1.7; }

.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.main-footer__column {
  flex-basis: 350px; }

.main-footer .site-logo {
  display: inline-block;
  margin-bottom: 55px; }

.main-footer__column-header {
  font: 15px/1 "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 25px; }

.main-footer__column-wrapper {
  display: flex;
  flex-direction: column;
  color: #C2C6C9; }

.main-footer__block--bottom {
  padding: 20px 0;
  color: #C2C6C9; }

.main-footer__block--bottom .main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.main-footer__bottom-info {
  text-transform: uppercase;
  font-size: 13px; }

.main-footer__bottom-info:not(:last-of-type) {
  margin-right: 100px; }

.social-links__icon {
  display: inline-block;
  margin-right: 8px; }

.advantages__wrapper {
  padding-top: 113px;
  padding-bottom: 113px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.advantages-item {
  flex-basis: 48.7%;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00B2FF;
  padding: 40px 40px 65px 40px;
  margin-bottom: 30px;
  cursor: pointer;
  transition: all 0.3s; }
  .advantages-item__icon {
    display: inline-block;
    margin-bottom: 25px;
    fill: #00B2FF; }
  .advantages-item__header {
    color: #00B2FF;
    font: 24px/1 "Roboto Condensed", sans-serif;
    margin-bottom: 25px; }

.advantages-item:hover {
  background: #00B2FF;
  color: #fff; }

.advantages-item:hover .advantages-item__header {
  color: #fff; }

.advantages-item:hover .advantages-item__icon {
  fill: #fff; }
